import React from "react";

import classNames from "classnames";
import { useAttemptGetTableQuery, useAttemptUpdateItemMutation } from "../redux/api";
import { checkEmail, checkNumbers, checkStrings } from "../helpers";

export const Table = () => {
  const [page, setPage] = React.useState(0);
  const [editable, setEditable] = React.useState(null);
  const { data, isError, isLoading } = useAttemptGetTableQuery(page);
  const [attemptUpdateItem] = useAttemptUpdateItemMutation();
  const [loading, setLoading] = React.useState(false);
  const [dataToUpdate, setDataToUpdate] = React.useState({});

  React.useEffect(() => {
    console.log(page);
  }, [page]);

  const onPageChange = async (idx) => {
    setLoading(true);

    await new Promise((res) => {
      setTimeout(res, 1000);
    });
    setLoading(false);
    setPage(idx);
  };

  const onEditableSelect = (item) => {
    console.log(item);
    setEditable(item);
  };
  
  const onEdit = (field, value) => {
   let canEdit = false;
    switch(field) {
      case 'email': {
        canEdit = checkEmail(value)
        break;
      }

      case 'name':
      case 'address': {
        canEdit = checkStrings(value);
        break;
      }

      case 'phone_number': {
        canEdit = checkNumbers(value);
        break;
      }

      default: {
        canEdit = false;
        break;
      }
    }

    if(canEdit) {
      setDataToUpdate({
        ...dataToUpdate,
        [field]: value,
      });

      return true;
    }
    const restoreValue = data.results.find(item => item.id === editable.id)[field];
    setDataToUpdate({
      ...dataToUpdate,
      [field]: restoreValue,
    });

    setEditable({
      ...editable,
      [field]: restoreValue,
    })

    return false;
  };
  
  const onSave = (editable, data) => {
    attemptUpdateItem({editable, data});
    setEditable(null)
    setDataToUpdate(null)
  }


  if (isLoading) {
    return (
      <div className="table-wrapper table-wrapper-empty">
        <div class="spinner-border spinner-border-table" role="status" />
      </div>
    );
  }

 
  return (
    <div className="table-wrapper">
      <table
        style={{
          opacity: loading ? "0.3" : "1",
          "pointer-events": loading ? "none" : "all",
        }}
      >
        <tr className="table-head">
          {data?.results &&
            Object.keys(data?.results[0])
              .slice(0)
              .map((title) => {
                return (
                  <th className="table-head-item" key={title}>
                    {title.charAt(0).toUpperCase() +
                      title.slice(1).replace(/_/gi, " ")}
                  </th>
                );
              })}
          <th className="table-head-item">EDIT</th>
          <th className="table-head-item">Save</th>
        </tr>
        {data.results &&
          data.results.map((item, i) => {
            return (
              <tr className="table-row" key={item.id} style={{borderBottom: editable && editable?.id === item?.id ? '1px solid rgba(120, 0, 0, 0.5)' : ''}} >
                <td className="table-row-item" contentEditable="false">
                  #{item.id}
                </td>
                <td
                  className="table-row-item"
                  contentEditable={editable && editable?.id === item?.id}
                  title={`Original Data: ${item.name}`}
                  onBlur={(e) => {
                    if(!onEdit("name", e.currentTarget.textContent)) return e.currentTarget.textContent = item.name;
                  }}
                >
                  {item.name}
                </td>
                <td
                  className="table-row-item"
                  contentEditable={editable && editable?.id === item?.id}
                  title={`Original Data: ${item.email}`}
                  onBlur={(e) => {
                    if(!onEdit("email", e.currentTarget.textContent)) return e.currentTarget.textContent = item.email;
                  }}
                >
                  {item.email}
                </td>
                <td
                  className="table-row-item"
                  contentEditable={editable && editable?.id === item?.id}
                  title={`Original Data: ${item.birthday_date}`}
                  onInput={(e) => {
                    onEdit("birthday_date", e.currentTarget.textContent);
                  }}
                >
                  {item.birthday_date}
                </td>

                <td
                  className="table-row-item"
                  contentEditable={editable && editable?.id === item?.id}
                  title={`Original Data: ${item.phone_number}`}
                  onBlur={(e) => {
                    if(!onEdit("phone_number", e.currentTarget.textContent)) return e.currentTarget.textContent = item.phone_number;
                  }}
                >
                  {item.phone_number}
                </td>

                <td
                  className="table-row-item"
                  contentEditable={editable && editable?.id === item?.id}
                  title={`Original Data: ${item.address}`}
                  onBlur={(e) => {
                   if(!onEdit("address", e.currentTarget.textContent)) return e.currentTarget.textContent = item.address;
                  }}
                >
                  {item.address}
                </td>
                <td className="table-row-item">
                  <button
                    type="button"
                    className={classNames(
                      "form-input-button",
                      "form-input-button--centered",
                      "form-input-button--edit",
                      {
                        "form-input-button--selected":
                          editable && editable?.id === item?.id,
                      }
                    )}
                    onClick={() => {
                      onEditableSelect(item);
                    }}
                    contentEditable="false"
                    style={{
                      pointerEvents:
                        editable && editable?.id === item?.id ? "none" : "all",
                    }}
                  >
                    {editable && editable?.id === item?.id
                      ? "Selected"
                      : "Edit"}
                  </button>
                </td>
                <td className="table-row-item">
                  {editable && editable.id === item.id && (
                    <button
                      type="button"
                      className="form-input-button form-input-button--centered form-input-button--save"
                      contentEditable="false"
                      onClick={() => {
                        
                        onSave(editable, dataToUpdate)
                    
                      }}
                    >
                      SAVE
                    </button>
                  )}
                </td>
              </tr>
            );
          })}
      </table>
      <div class="divider"></div>
      <nav className="table-pagination">
        {data.count &&
          Array.from(
            {
              length:
                data.count % 10 === 0 ? data.count / 10 : data.count / 10 + 1,
            },
            (_, i) => i
          )
            .fill(1)
            .map((_, i, arr) => {
              return (
                <span
                  className={classNames("table-pagination-item", {
                    "table-pagination-item-active": i === page,
                  })}
                  key={i}
                  onClick={() => onPageChange(i)}
                >
                  {" "}
                  {i + 1}
                </span>
              );
            })}
      </nav>
      {loading && (
        <div class="spinner-border spinner-border-table" role="status" />
      )}
    </div>
  );
};
