
//styles
import React, { useEffect, useState } from 'react';
import './App.css';
import "bootstrap-icons/font/bootstrap-icons.css"
import 'bootstrap/dist/css/bootstrap.min.css';
//hooks for api
import { useAttemptGetTableQuery, useAttemptLoginMutation, useGetAllQuery } from './redux/api';
//form validation tools
import * as formik from 'formik';
import * as yup from 'yup';
//react hooks 

//components
import { Table } from './Components/Table';

function App() {
  const { Formik } = formik;
  const [credentials, setCredentials] = useState({});
  const [isLoading, setLoading] = useState(false)
  const [isAuth, setAuth] = useState(false);
  const [attemptLogin, { status, error, data }] = useAttemptLoginMutation();



  const schema = yup.object().shape({
    username: yup.string()
      .required('Username is required')
      .min(1, 'Username is too short')
      .max(150, 'Username is too long'),
    password: yup.string()
      .required('Password is required')
      .min(1, 'Password is too short')
      .max(128, 'Password is too long'),
  });


  useEffect(() => {
    if (data?.length) {
      setAuth(true)
    }
  }, [data]);



  const setLoginData = async (loginCredentials) => {
    setLoading(true);

    await new Promise((res) => {
      setTimeout(res( attemptLogin(loginCredentials)), 1000)
    })

   
    setLoading(false)
  };
  if (isAuth) return (<Table></Table>)
  return (
    <Formik
      validationSchema={schema}
      onSubmit={setLoginData}
      initialValues={() => ({
        username: '',
        password: '',
      })}
    >
      {({ handleSubmit, handleChange, handleBlur, values, touched, errors }) => (
        <div className="form-wrapper">
          <form className="form" onSubmit={handleSubmit} style={{ border: error ? '1px solid rgba(212, 50, 50, 0.3)' : '1px solid rgba(76, 164, 247, 0.3)' }}>
            <h1 className="form-title">Sign in to Table</h1>
            <div className="form-input-wrapper">
              {credentials.username}
              {credentials.password}

              <input
                onChange={handleChange}
                className="form-input"
                id="username"
                name="username"
                value={values.username}
                placeholder='Username or email'
                type="text"
                onBlur={handleBlur}
                style={{ border: touched.username && !errors.username && !error && "1px solid green" }}
              />
              <label
                className="form-input-hidden"
                htmlFor="username">
                Username or email
              </label>
              {touched.username && !errors.username && !error && <i className="bi bi-check2-circle form-check" title="Success"></i>}
              {touched.username && errors.username || error ? <i className="bi bi-x-circle form-check  form-check-failed" title="Invalid input value"></i> : ''}
              {errors.username && (
                <span className="form-error">
                  <i class="bi bi-exclamation-circle"></i>
                  {errors.username}
                </span>
              )}
            </div>

            <div className="form-input-wrapper">
              <input

                className="form-input"
                placeholder='Password'
                id="password"
                name="password"
                value={values.password}
                type="password"
                onChange={handleChange}
                onBlur={handleBlur}
                style={{ border: touched.password && !errors.password && !error && "1px solid green" }}
              />
              <label
                htmlFor="password"
                className="form-input-hidden"
              >Password
              </label>
              {touched.password && !errors.password && !error && <i className="bi bi-check2-circle form-check" title="Success"></i>}
              {touched.password && errors.password || error ? <i className="bi bi-x-circle form-check  form-check-failed" title="Invalid input value"></i> : ''}
              {touched.password && errors.password && (
                <span className="form-error">
                  <i class="bi bi-exclamation-circle"></i>
                  {errors.password}
                </span>
              )}
              {error && !errors.username && !errors.password && (
                <div class="form-error-bottom">
                  <span className="form-error">
                    <i class="bi bi-exclamation-circle"></i>
                    {error}
                  </span>
                </div>
              )}

              {data && (<div class="form-error-bottom">
                <span className="form-error form-error-success">
                  <i class="bi bi-check2-circle"></i>
                  {data}
                </span>
              </div>
              )}
            </div>
            <button disabled={errors.password || errors.username} type="submit" className="form-input-button">
              {isLoading ? (<div class="spinner-border" role="status" />) : 'Sign In'}
            </button>

          </form>
        </div>
      )}
    </Formik>
  );
}

export default App;