import { createApi, fetchBaseQuery } from '@reduxjs/toolkit/query/react';

export const testApi = createApi({
    reducerPath: 'testApi',
    baseQuery: fetchBaseQuery({ baseUrl: 'https://technical-task-api.icapgroupgmbh.com/api' }),
    tagTypes: ['Table'],
    endpoints: (build) => ({
        attemptGetTable: build.query({
            query: (idx) => `/table/?limit=10&offset=${idx * 10}`,
            providesTags: (result) => result.results ? result?.results.map(({ id }) => ({ type: 'Table', id })) : ['Table'],
        }),
       


        attemptLogin: build.mutation({
            query: (data) => {
                console.log(data)
                return ({
                    url: '/login/',
                    method: 'POST',
                    body: JSON.stringify(data),
                    headers: {
                        'Content-type': 'application/json; charset=UTF-8',
                    },
                })
            },

            transformResponse: (response, meta, arg) => {
                return response.message;
            },

            transformErrorResponse: (error, _, __) => {
                console.log(error)
                switch (error.data.error) {
                    case 'Invalid credentials.': {
                        return 'Invalid password or/and email';
                    } default: {
                        return 'Oooops, something went wrong...'
                    }
                }
            }
        }),

        attemptDeleteItem: build.mutation({
            query: (id) => ({
                url: `table/${id}`,
                method: 'DELETE',
            })
        }),

        attemptUpdateItem: build.mutation({
            query: ({editable, data}) => {
                 alert(JSON.stringify(data))
                return  ({
                    url: `/table/${editable.id}/`,
                    method: 'PATCH',
                    body: JSON.stringify(data),
                    headers: {
                        'Content-type': 'application/json; charset=UTF-8',
                    },
                })
            },

            transformResponse: (response, meta, arg) => {
                alert(JSON.stringify(response), 'response!')
            },

            transformErrorResponse: (error, _, __) => {
                alert(error.data.error, 'error!')
            },
            invalidatesTags: (result, error, arg) => [{ type: 'Table', id: arg.id}]
        })
    }),

})

export const {
    useGetAllQuery,
    useAttemptLoginMutation,
    useAttemptDeleteItemMutation,
    useAttemptUpdateItemMutation,
    useAttemptGetTableQuery,
} = testApi;